	export default {
		data() {
			return {
				list:[]
			};
		},
		onShow() {
			this.getList();
		},
		methods:{
			AddOrUpdate(id){
				let url = "../addAddress/addAddress";
				if(typeof id == 'number'){
					url = "../addAddress/addAddress?addressId="+id;
				}
				uni.navigateTo({
					url:url
				})
			},
			backPage(id){
				console.log('返回页面')
				let pages = getCurrentPages();
				let prePage = pages[pages.length - 2];
				console.log(prePage.route)
				if(prePage.route == 'pages/shopCart/applyOrders/applyOrders'){
					try{
						uni.setStorageSync('addressId', id);
// 						uni.navigateTo({
// 							url:'/pages/shopCart/applyOrders/applyOrders'
// 						})
						uni.navigateBack();
					}catch(e){
						//TODO handle the exception
						console.log('地址缓存失败')
					}
				}
				




			},
			getList(){
				this.$http.request({
					url:this.$api.AddressList,
					method:'POST'
				}).then(data => {
					console.log(data)
					this.list = data.data || [];
				})
			}
		}
	}
